<template>
	<v-container fluid class="tab">
		<div
			v-if="loading"
			height="100%"
			class="d-flex justify-center align-center margin-top"
		>
			<v-progress-circular
				:size="50"
				color="primary"
				indeterminate
			></v-progress-circular>
		</div>
		<section v-else>
			<div class="mb-5" id="btn-links">
				<router-link
					:to="{ name: 'Pet' }"
					v-slot="{ href, navigate, isActive }"
					custom
					class="mr-3"
				>
					<v-btn :class="[isActive && 'router-link-active']">
						<a
							:href="href"
							@click="navigate"
							class="text-decoration-none black--text"
						>
							Pet
						</a>
					</v-btn>
				</router-link>

				<router-link
					:to="{ name: 'ProductsList' }"
					v-slot="{ href, navigate, isActive }"
					custom
				>
					<v-btn :class="[isActive && 'router-link-active']">
						<a
							:href="href"
							@click="navigate"
							class="text-decoration-none black--text"
						>
							Store
						</a>
					</v-btn>
				</router-link>
			</div>
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</section>
	</v-container>
</template>

<script>
	export default {
		data() {
			return {
				loading: true,
				isActive: true,
			};
		},

		created() {
			Promise.all([
				this.$store.dispatch('getDogBreedsNames'),
				this.$store.dispatch('getCatBreedsNames'),
			]).then(() => {
				this.loading = false;
			});
		},

		methods: {},
	};
</script>

<style lang="scss" scoped>
	@import '../../assets/styles/main.scss';

	.tab {
		width: 80%;
	}

	.margin-top {
		margin-top: 30vh;
	}

	#btn-links .router-link-active {
		background-color: #64ffda;
	}
</style>
